.topMenuContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 1rem;
  max-width: 50%;
  max-height: 10%;
  padding: 1rem;
  width: auto;
}

.addButton {
  box-shadow: 3px 4px 0px 0px #c43421;
  background: linear-gradient(to bottom, #f99e2a 5%, #db3b25 100%);
  background-color: #f99e2a;
  border-radius: 5px;
  border: 1px solid #c43421;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 17px;
  font-weight: bold;
  padding: 0.5rem 1rem;
  text-decoration: none;
  text-shadow: 0px 1px 0px #c43421;
}

.addButton:hover {
  background: linear-gradient(to bottom, #db3b25 5%, #f99e2a 100%);
  background-color: #db3b25;
}

.addButton:active {
  position: relative;
  top: 1px;
}

.filter {
  align-items: center;
  display: flex;
  padding: 1rem;
  justify-content: center;
}

.filterLabel {
  white-space: nowrap;
}

li.filterItem {
  list-style-type: none;
  margin: 0 0 0 7px;
  padding: 0;
}
